
.about-section {
	background: #dfe9ff;
}

.site-features__wrapper {
	position: relative;
	margin-top: -8rem;
	background: #d9f3da;
	border-radius: 5px;
	padding: 1.2rem;
	box-shadow: 0px 0px 19px 0px #7b7b7b;
	margin-bottom: 2rem;
}

.site-features,
.connect-features {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}

.site-features__intro {
	margin: 0;
}

.site-features p {
	margin: .5rem;
	font-weight: 500;
}

.about__pic {
	width: 100%;
	height: auto;
	max-width: 400px;
}

.project__featured-image {
	width: 100%;
	height: auto;
}

.card {
	text-align: center;
}

@media screen and (max-width: 960px) {
	.site-features__wrapper {
		margin-top: -4rem;
	}
}
