.toggle-button {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	height: 35px;
	width: 40px;
	background: transparent;
	border: none;
	cursor: pointer;
	margin: 1.2rem;
	padding: 0;
	box-sizing: border-box;
}

.toggle-button:focus {
	outline: none;
}

.toggle-button__line {
	width: 30px;
	height: 3px;
	background: white;
}
